// src/components/ui/ProgressBar/useProgressAnimation.js
import { useEffect, useRef } from "react";
import { Animated } from "react-native";

// Hook pour l'animation - toujours appelé
function useAnimatedProgressValue(progress, duration) {
  const animatedValue = useRef(new Animated.Value(0)).current;
  
  useEffect(() => {
    Animated.timing(animatedValue, {
      toValue: progress,
      duration,
      useNativeDriver: false,
    }).start();
  }, [progress, duration, animatedValue]);
  
  return animatedValue;
}

// Hook pour la progression statique - toujours appelé
function useStaticProgressValue(progress) {
  const staticValue = useRef(progress).current;
  return staticValue;
}

export default function useProgressAnimation({
  progress = 0,
  animated = true,
  duration = 600,
}) {
  const clamped = Math.min(Math.max(progress, 0), 100);
  
  // Les deux hooks sont toujours appelés, mais seul le résultat approprié est utilisé
  const animatedValue = useAnimatedProgressValue(clamped, duration);
  const staticValue = useStaticProgressValue(clamped);

  // Calcul de la largeur basé sur les valeurs des hooks
  const width = animated 
    ? animatedValue.interpolate({
        inputRange: [0, 100],
        outputRange: ["0%", "100%"],
      })
    : `${staticValue}%`;

  return { width };
}


